

/*Navbar*/
.navbar {
    background-color: #B5CDA3;
    font-family: 'Poppins', sans-serif;
}

.navbar li:hover {
    background-color:  #b9e0c9;
    color: white;   
}

/*Homepage*/
.homepage{
   padding: 5%;
}

p.welcome {
    font-family: 'Sacramento', cursive;
    font-size: 75px;
    padding-top: 15%;
    font-weight: bold;
    
}

p.content {
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    padding-top: 1%;
   
}

.homepage img{
   float: right;
   border: 20px solid #B5CDA3;
   animation-name: example;
  animation-duration: 3s;
}

@keyframes example {
    from {border: 20px solid #C1AC95;}
    to {border: 20px solid #C9E4C5;}
  }

/*Saturday*/

p.title{
    font-family: 'Sacramento', cursive;
    font-weight: bold;
    font-size: 70px;
    text-align: left;
}


h3{
    font-family: 'Dancing Script', cursive;
    text-align: center;
    padding-top: 2%;
}

p{
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    padding-top: 5%;
}

.saturday{
    padding: 10%;
}

.col img{
   margin:auto; 
   display: block;
}

.col{
    padding-bottom: 5%;
}

.card-group{
    box-shadow: 0px 2px 12px 4px rgba(0,0,0,0.6);
-webkit-box-shadow: 0px 2px 12px 4px rgba(0,0,0,0.6);
-moz-box-shadow: 0px 2px 12px 4px rgba(0,0,0,0.6);
}
.card img{
    width: 70%;
}

.card-body{
    text-align: center;
    padding: 10%;
}



/*Sunday*/
.sunday img{
  display: block;
  margin: auto;
}

.figure img{
    width: 50%;
    margin: auto;
    display: block;
}


/*Footer*/
.page-footer{
    background-color: #7aa659;
}

a{
    text-decoration: none;
    color: white;
    cursor: pointer;
}


a:hover{

    color: yellow;
    cursor: pointer;
}

/*mix*/
.card mix{
    border:5px solid #95D1CC;
}